
import XHeader from 'vux/src/components/x-header/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
components: {
XHeader,
XSwitch,
XButton,
Group,
Cell,
Box,
Alert,
AlertModule
},
data() {
return {
autoOpenEqcode: false,
currentVersion: "1.0"
};
},
mounted(){
if ("undefined" != typeof api) {
this.currentVersion = api.version;
}
},
methods: {
checkUpdate: function() {
if ("undefined" == typeof api) {
AlertModule.show({ title: "提示", content: "本功能需要apicloud支持，请打包后使用" });
return;
}

var mam = api.require("mam");
mam.checkUpdate(function(ret, err) {
if (ret) {
var result = ret.result;
if (result.update == true && result.closed == false) {
var str =
"新版本型号:" +
result.version +
";\r\n更新提示语:" +
result.updateTip +
";\r\n发布时间:" +
result.time;
api.confirm(
{
title: "有新的版本,是否下载并安装 ",
msg: str,
buttons: ["确定", "取消"]
},
function(ret, err) {
if (ret.buttonIndex == 1) {
if (api.systemType == "android") {
api.download(
{
url: result.source,
report: true
},
function(ret, err) {
if (ret && 0 == ret.state) {
/* 下载进度 */
api.toast({
msg: "正在下载应用" + ret.percent + "%",
duration: 2000
});
}
if (ret && 1 == ret.state) {
/* 下载完成 */
var savePath = ret.savePath;
api.installApp({
appUri: savePath
});
}
}
);
}
if (api.systemType == "ios") {
api.installApp({
appUri: result.source
});
}
}
}
);
} else {
AlertModule.show({ title: "提示", content: '暂无更新' });
}
} else {
AlertModule.show({ title: "提示", content: err.msg });
}
});
}
}
};



