
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'

var timeout;
export default {
components: {
XHeader,
XInput,
Group,
XButton,
Box
},
data() {
return {
username: "",
password: "",
timeSec: 60,
timeValidButton: "发送验证码"
};
},
created(){
console.log("created");
},
methods: {
resetTime:function() {
this.timeSec = 0;
}
},
watch:{
$router:'cleanValidTime'
},
mounted() {
var that = this;
timeout = setInterval(function() {
console.log(that.timeSec);
that.timeSec = that.timeSec + 1;
if (that.timeSec >= 60) {
that.timeValidButton = "发送验证码";
} else {
that.timeValidButton = 60 - that.timeSec + "秒";
}
}, 1000);
},
beforeRouteLeave(to, from, next){
if(timeout){
console.log("clearTimeout");
clearTimeout(timeout);
}
next();
}
};




